
import Vue from 'vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import VideoPlayer from 'vue-video-player'
require('video.js/dist/video-js.css')
require('vue-video-player/src/custom-theme.css')
import {getCookie} from '@/tool/common.js';
import Drive from '@/tool/classFactory/drive.js';
Vue.use(VideoPlayer)
export default {
components: {
Group,
Cell
},
data () {
return {
msg: '请点击视频按钮观看,视频仅供参考！',
playerOptions : {
playbackRates: [0.7, 1.0, 1.5, 2.0], //播放速度
autoplay: false, //如果true,浏览器准备好时开始回放。
muted: false, // 默认情况下将会消除任何音频。
loop: false, // 导致视频一结束就重新开始。
preload: 'auto', // 建议浏览器在<video>加载元素后是否应该开始下载视频数据。auto浏览器选择最佳行为,立即开始加载视频（如果浏览器支持）
language: 'zh-CN',
aspectRatio: '16:9', // 将播放器置于流畅模式，并在计算播放器的动态大小时使用该值。值应该代表一个比例 - 用冒号分隔的两个数字（例如"16:9"或"4:3"）
fluid: true, // 当true时，Video.js player将拥有流体大小。换句话说，它将按比例缩放以适应其容器。
sources: [{
type: "",
src: "" 
}],
poster: "", //你的封面地址

notSupportedMessage: '此视频暂无法播放，请稍后再试', //允许覆盖Video.js无法播放媒体源时显示的默认信息。
controlBar: {
timeDivider: true,
durationDisplay: true,
remainingTimeDisplay: false,
fullscreenToggle: true  
}
},
params:{
'id':'',
'sessionId':''
},
video:{

}
}
},
created () {
this.params.id = this.$route.query.videoId || null
this.params.sessionId = this.getCookie('sessionId')
console.log(this.params)
this.getVideoInfo()
},
methods: {
getCookie: function (objName) {
var arrStr = document.cookie.split("; ")
for (var i = 0; i < arrStr.length; i++) {
var temp = arrStr[i].split("=")
if(temp[0] == objName)
return unescape(temp[1])
}
},
getVideoInfo () {
const that = this;
Drive.prototype.getVideoInfo(that.params).then(res => {
let list = res.data.data || {};
that.video = list
that.playerOptions.sources[0].src = list.url
});
},
onVideoTap (item){
console.log(item)
this.$router.push('../video?videoId=' + item.id)
}
}
}



